import React, { useContext, useEffect, useState } from "react";
import "./SubmitDesign.css";
import { postData, serverURL } from "../../services/NodeServices";
import { toast } from "react-toastify";
import { UserContext } from "../../context/UserContext";

const ImageUploadComp = () => {
  const [designType, setDesignType] = useState("");
  const [images, setImages] = useState([]);
  const [imageSelected, setImageSelected] = useState(false);
  const [error, setError] = useState("");
  const {user} =useContext(UserContext)
  const [deisgncount,setDesignCount]=useState(0)


  const getDesignCount=async()=>{
    const myuserr = JSON.parse(localStorage.getItem('user'))
    console.log(myuserr)
    let body={
      userid:myuserr.userid
    }
    const result = await postData('api/users/designbyid',body)
    if(result.status){
      setDesignCount(result.data.designCount)
      console.log(result.data.designCount)
    }
  }
  useEffect(()=>{
   setTimeout(()=>{
    getDesignCount()
   },511)
  },[])
  const loadRazorpayScript = () => {
    return new Promise((resolve, reject) => {
      const script = document.createElement("script");
      script.src = "https://checkout.razorpay.com/v1/checkout.js";
      script.onload = () => resolve(true);
      script.onerror = () =>
        reject(new Error("Failed to load Razorpay script"));
      document.body.appendChild(script);
    });
  };

  const handleDesignChange = (e) => {
    setDesignType(e.target.value);
  };
   
  const handleImageChange = (e) => {
    if (images.length >= 2) {
      setError("You can upload up to 1 images only.");
      return;
    }
    setError("");
    const selectedFiles = Array.from(e.target.files);
    const updatedImages = [...images, ...selectedFiles].slice(0, 5);
    setImages(updatedImages);
    setImageSelected(true);
  };

  const handleSubmit = async () => {
    if (!designType) {
      setError("Please select a design decision type.");
      return;
    }
    if (images.length === 0) {
      setError("Please upload at least one image.");
      return;
    }
    setError("");

    
    const formData = new FormData();
    formData.append("category", designType);
    formData.append("userid",user.userid);
    images.forEach((image) => {
      formData.append("images", image);
    });
    
  try{
    const result = await postData('api/design/submit2',formData)
    
    if(result.status){
        toast.success('design submitted Succesfully')
        setImages([])
        setImageSelected(false)

    }else{
      
    }
  }catch{

  }
      
  };

  const handlePayandSubmit=async()=>{


 if (!designType) {
      setError("Please select a design decision type.");
      return;
    }
    if (images.length === 0) {
      setError("Please upload at least one image.");
      return;
    }
    setError("");

   
    setError("");

    loadRazorpayScript()

    const response = await fetch(`${serverURL}create-order3`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ amount: 100 }),
    });

    const order = await response.json();

    const options = {
      key: "rzp_test_nt89OO93hs7lOa",
      amount: order.amount,
      currency: order.currency,
      name: "User Registration",
      description: "Payment for submitting design",
      order_id: order.id,
      handler: async (paymentResponse) => {
        console.log(paymentResponse)
          
        
        
        const verifyResponse = await fetch(`${serverURL}verify-payment3`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            payment_id: paymentResponse.razorpay_payment_id,
            order_id: paymentResponse.razorpay_order_id,
            signature: paymentResponse.razorpay_signature,
            userid:user.userid
          }),
        });

        const verificationResult = await verifyResponse.json();
        console.log(verificationResult)

        if (verificationResult.success) {

          
          const formData = new FormData();
    formData.append("category", designType);
    formData.append("userid",user.userid);
    images.forEach((image) => {
      formData.append("images", image);
    });
    
  try{
    const result = await postData('api/design/submit',formData)
    
    if(result.status){
        toast.success('design submitted Succesfully')
        setImages([])
        setImageSelected(false)
        toast.success('design submitted succesfully')

    }else{
      
    }
  }catch{

  }
           
        

        } else {
          setError("Payment verification failed. Please try again.");
        }
      },
      prefill: {
        name: "User Name",
        email: "user@example.com",
      },
      theme: {
        color: "#3399cc",
      },
    };

    const razorpay = new window.Razorpay(options);
    razorpay.open();
  }


  const handleCancel = () => {
    setImages([]);
    setImageSelected(false);
  };


  console.log(user.group)
  return (
    <div className="submaincontainer">
      <div className="upload-container">
        <h2>Submit Design</h2>
        <div className="form-groupp">
          <label htmlFor="designType">Select Design Type</label>
          <div className="selectdesigntype">
           {user?.group=='18+'&&<> <p
              style={{
                backgroundColor: designType == "GYM" ? "#2f2960" : "#fff",color: designType == "GYM" ? "#fff" : "#222",
              }}
              onClick={() => {
                setDesignType("GYM");
              }}
            >
              GYM
            </p>
            <p
              style={{
                backgroundColor: designType == "Music" ? "#2f2960" : "#fff",color: designType == "Music" ? "#fff" : "#222",
              }}
              onClick={() => {
                setDesignType("Music");
              }}
            >
              Music
            </p></>}
            <p
              style={{
                backgroundColor:
                  designType == "Sustainability" ? "#2f2960" : "#fff",color: designType == "Sustainability" ? "#fff" : "#222",
              }}
              onClick={() => {
                setDesignType("Sustainability");
              }}
            >
              Sustainability
            </p>
          {user?.group=='18+'&&<>  <p
              style={{
                backgroundColor: designType == "Gaming" ? "#2f2960" : "#fff",color: designType == "Gaming" ? "#fff" : "#222",
              }}
              onClick={() => {
                setDesignType("Gaming");
              }}
            >
              Gaming
            </p>
            <p
              style={{
                backgroundColor: designType == "Travel" ? "#2f2960" : "#fff",color: designType == "Travel" ? "#fff" : "#222",
              }}
              onClick={() => {
                setDesignType("Travel");
              }}
            >
              Travel
            </p>
            <p
              style={{
                backgroundColor:
                  designType == "Social Thoughts" ? "#2f2960" : "#fff",color: designType == "Social Thoughts" ? "#fff" : "#222",
              }}
              onClick={() => {
                setDesignType("Social Thoughts");
              }}
            >
              Social Thoughts
            </p></>}
          </div>
        </div>
        <div className="form-groupp">
          <label htmlFor="imageUpload">Upload Images:</label>

          <div className="imageuploadd">
            <div style={{ paddingLeft: 38, marginTop: 15, cursor: "pointer" }}>
              {!imageSelected ? (
                <img
                  src="/drag.png"
                  style={{ width: 221 }}
                  onClick={() => document.getElementById("imageUpload").click()}
                />
              ) : (
                <div className="uploadedimage">
                  {" "}
                  <img
                    style={{ width: 221 }}
                    src={URL.createObjectURL(images[0])}
                  />
                  <p onClick={handleCancel}>x</p>
                </div>
              )}
            </div>
            <input
              type="file"
              id="imageUpload"
              multiple
              accept="image/*"
              onChange={handleImageChange}
              className="hidden-input"
              style={{ display: "none" }}
            />
          </div>
        </div>
        {/*   <div className="preview-container">
        {images.map((image, index) => (
          <div key={index} className="image-preview">
            <img src={URL.createObjectURL(image)} alt={`upload-${index}`} />
          </div>
        ))}
      </div> */}
        {error && <div className="error">{error}</div>}

        <div className="sub">
          <button onClick={()=>{deisgncount<2?handleSubmit():handlePayandSubmit()}} className="submit-button">
            {deisgncount==0?'Submit ( 2 Designs left) ':deisgncount==1?'Submit ( 1 Designs left)':'Pay 100/-  and Submit'}
          </button>
        </div>
      </div>
    </div>
  );
};

export default ImageUploadComp;
