import React, { useContext } from "react";
import { useState,useEffect } from "react";
import './Userpanel.css'
import { Route, Routes, useNavigate } from "react-router-dom";
import ImageUploadComp from "../../components/SubmitDesign/SubmitDesign";
import MySubmissions from "../../components/MySubmissions/MySubmissions";
import { UserContext } from "../../context/UserContext";
import MyWins from "../../components/MyWins/MyWins";
import UserProfile from "../../UserProfile/UserProfile";


export default function UserPanel(){

   const navigate = useNavigate()

   const { user,setisLoggedIn} = useContext(UserContext);
  
   const handleLogOut=()=>{
  
        localStorage.clear()
        setisLoggedIn(false)
        navigate('/')
  
   }
   const handleNavLinkClick = (link) => {
    
    setMiniNav(false)
     
     navigate(link)
  };



   const [miniNav,setMiniNav]=useState(false)

    return(
        <div className="userpanel__container">

            <div className="userpanel__innr">
            <div className="dashboard__leftnav">
              <img src="/menu.png" onClick={() => setMiniNav(!miniNav)} />
           </div>
              
              <div className="userpanel__l">
                <div className="userpanel__header">
                  Clanfest
                </div>
               
                <div className="userpanellinks">
                <div className="userpanelitem" onClick={()=>{handleNavLinkClick('/user/profile')}}>
                     Profile
                    </div>
                    <div className="userpanelitem" onClick={()=>{handleNavLinkClick('/user/')}}>
                     Submit
                    </div>
                    <div className="userpanelitem" onClick={()=>{handleNavLinkClick('/user/submissions')}}>
                     My Submissions
                    </div>
                    <div className="userpanelitem" onClick={()=>{handleNavLinkClick('/user/mywins')}}>
                     My Wins
                    </div>
                    <div className="userpanelitem2" onClick={handleLogOut}>
                     Logout
                    </div>

                </div>
             

              </div>
              <div className="userpanel__l2" style={{transform:!miniNav?'translateX(-111%)':'',transition: 'transform 0.3s ease',}}>
                <div className="userpanel__header">
                  Clanfest
                </div>
               
                <div className="userpanellinks">
                <div className="userpanelitem" onClick={()=>{handleNavLinkClick('/user/profile')}}>
                     Profile
                    </div>
                    <div className="userpanelitem" onClick={()=>{handleNavLinkClick('/user/')}}>
                     Submit
                    </div>
                    <div className="userpanelitem" onClick={()=>{handleNavLinkClick('/user/submissions')}}>
                     My Submissions
                    </div>
                    <div className="userpanelitem" onClick={()=>{handleNavLinkClick('/user/mywins')}}>
                     My Wins
                    </div>
                    <div className="userpanelitem2" onClick={handleLogOut}>
                     Logout
                    </div>

                </div>
             

              </div>
              <div className="userpanel__r">
                <Routes>
                  <Route path="/profile" element={<UserProfile user={user}/>}/>

                    <Route path="/" element={<ImageUploadComp/>}/>
                    <Route path="/submissions" element={<MySubmissions/>}/>
                    <Route path="/mywins" element={<MyWins/>}/>
                </Routes>

              </div>
           


            </div>
            

        </div>
    )
}