import React, { useEffect } from 'react';


const Cancel = () => {

    useEffect(() => {
        window.scrollTo({
            top: 0,
            behavior: 'smooth', // Smooth scroll animation
        });
    }, []);

    return (
        <div className='maincont'>
            <div className="privacy-policy-container">
                <h1 className="privacy-policy-title">Cancellation and Refund Policy</h1>
                <h2 className="privacy-policy-subtitle">1. Non-Refundable Registration Fees</h2>
                <ul className="privacy-policy-list">
                    <li>Registration fees paid for participation in the competition are strictly non-refundable.</li>
                    <li>This policy applies regardless of the circumstances, including but not limited to voluntary cancellation by the participant, failure to attend the competition, or any other reason not attributable to the organizers.</li>
                </ul>

                <h2 className="privacy-policy-subtitle">2. Payment Issues</h2>
                <ul className="privacy-policy-list">
                    <li>In the event of technical issues during the payment process, participants must report the issue to <a href="mailto:support@blazestore.in" className="privacy-policy-link">support@blazestore.in</a> within seven (7) calendar days from the date of payment.</li>
                    <li>The organizers will make reasonable efforts to resolve the issue. However, any delay in reporting may result in the inability to process claims, for which the organizers will not be held liable.</li>
                </ul>

                <h2 className="privacy-policy-subtitle">3. Acknowledgment and Agreement</h2>
                <ul className="privacy-policy-list">
                    <li>By registering for the competition, participants acknowledge and agree to adhere to the terms outlined in this policy.</li>
                </ul>
            </div>
        </div>
    );
};

export default Cancel;
