import React, { useState, useEffect } from 'react';
import './Countdown.css';

const CountdownTimer = () => {
  const calculateTimeLeft = () => {
    const now = new Date();
    const nextYear = now.getFullYear() ;
    const targetDate = new Date(`February 15, ${nextYear} 00:00:01`); // Corrected spelling and set to February 15
    const difference = targetDate - now;

    const seconds = Math.floor(difference / 1000);
    return seconds > 0 ? seconds : 0; // Ensure no negative values
  };

  const [timeLeft, setTimeLeft] = useState(calculateTimeLeft);

  useEffect(() => {
    if (timeLeft === 0) return;
    const timerId = setInterval(() => {
      setTimeLeft(calculateTimeLeft());
    }, 1000);
    return () => clearInterval(timerId);
  }, [timeLeft]);

  const formatTime = (seconds) => {
    const days = Math.floor(seconds / (3600 * 24));
    const hours = Math.floor((seconds % (3600 * 24)) / 3600);
    const minutes = Math.floor((seconds % 3600) / 60);
    const remainingSeconds = seconds % 60;

    return {
      days: String(days).padStart(2, '0'),
      hours: String(hours).padStart(2, '0'),
      minutes: String(minutes).padStart(2, '0'),
      remainingSeconds: String(remainingSeconds).padStart(2, '0'),
    };
  };

  const { days, hours, minutes, remainingSeconds } = formatTime(timeLeft);

  return (
    <div className="countdown-container">
      <div className="top-text">Hurry up!! Registration ends on 14th February 2025.</div>
      <div className="timer">
        <div className="time-box">
          <div className="time">{days}</div>
          <div className="label">Days</div>
        </div>
        <div className="time-box">
          <div className="time">{hours}</div>
          <div className="label">Hours</div>
        </div>
        <div className="time-box">
          <div className="time">{minutes}</div>
          <div className="label">Minutes</div>
        </div>
        <div className="time-box">
          <div className="time">{remainingSeconds}</div>
          <div className="label">Seconds</div>
        </div>
      </div>
    </div>
  );
};

export default CountdownTimer;
