import React, { useEffect } from 'react';
import './PrivacyPolicy.css'; // Import the CSS file

const PrivacyPolicy = () => {

    useEffect(() => {
        window.scrollTo({
            top: 0,
            behavior: 'smooth', // Smooth scroll animation
        });
    }, []);

    return (
        <div className='maincont'>
            <div className="privacy-policy-container">
                <h1 className="privacy-policy-title">Privacy Policy </h1>
                <p>
                    Blaze is dedicated to safeguarding your privacy and ensuring the protection of your personal information. This Privacy Policy outlines how Blaze collects, uses, stores, and shares your data in connection with the Blaze Digital Art Competition. By participating in the competition, you acknowledge and agree to the terms and practices described in this policy.
                </p>

                <h2 className="privacy-policy-subtitle">1. Information We Collect</h2>
                <h2 className="privacy-policy-subsection">a. Personal Information:</h2>
                <ul className="privacy-policy-list">
                    <li>Full name and age.</li>
                    <li>Contact details, including email address, phone number, and mailing address.</li>
                    <li>Proof of identity or age (if required for eligibility verification).</li>
                </ul>
                <h2 className="privacy-policy-subsection">b. Art Submissions:</h2>
                <ul className="privacy-policy-list">
                    <li>Submitted artwork, including digital files or physical entries.</li>
                    <li>Descriptions, titles, or metadata associated with your submissions.</li>
                </ul>
                <h2 className="privacy-policy-subsection">c. Technical Information:</h2>
                <ul className="privacy-policy-list">
                    <li>Internet Protocol (IP) address and device-specific details when accessing our website.</li>
                    <li>Browser type, usage data, and other analytics for website optimization and analysis.</li>
                </ul>

                <h2 className="privacy-policy-subtitle">2. How We Use Your Information</h2>
                <h2 className="privacy-policy-subsection">a. Competition Management:</h2>
                <ul className="privacy-policy-list">
                    <li>To verify eligibility, manage registration, evaluate submissions, and select winners.</li>
                </ul>
                <h2 className="privacy-policy-subsection">b. Communication:</h2>
                <ul className="privacy-policy-list">
                    <li>To provide updates about the competition, results, and related announcements.</li>
                </ul>
                <h2 className="privacy-policy-subsection">c. Marketing and Promotion:</h2>
                <ul className="privacy-policy-list">
                    <li>To display selected submissions, including participant names and artwork, for public polls, on the Blaze website, or in promotional materials.</li>
                </ul>
                <h2 className="privacy-policy-subsection">d. Analytics:</h2>
                <ul className="privacy-policy-list">
                    <li>To analyze website usage and improve our services, user experience, and competition processes.</li>
                </ul>

                <h2 className="privacy-policy-subtitle">3. Sharing of Information</h2>
                <h2 className="privacy-policy-subsection">a. Public Display:</h2>
                <ul className="privacy-policy-list">
                    <li>Selected submissions, including participant names and artwork, may be displayed on Blaze’s website, social media platforms, or other promotional channels.</li>
                </ul>
                <h2 className="privacy-policy-subsection">b. Service Providers:</h2>
                <ul className="privacy-policy-list">
                    <li>Trusted third-party service providers (e.g., web hosting and marketing partners) assisting in managing the competition, under strict confidentiality obligations.</li>
                </ul>
                <h2 className="privacy-policy-subsection">c. Legal Compliance:</h2>
                <ul className="privacy-policy-list">
                    <li>To comply with legal requirements, enforceable government requests, or court orders.</li>
                </ul>

                <h2 className="privacy-policy-subtitle">4. Data Storage and Security</h2>
                <p>
                    Blaze employs industry-standard measures to secure your data against unauthorized access, alteration, or loss.
                </p>
                <h2 className="privacy-policy-subsection">a. Access Control:</h2>
                <ul className="privacy-policy-list">
                    <li>Personal information is accessible only to authorized personnel bound by confidentiality agreements.</li>
                </ul>
                <h2 className="privacy-policy-subsection">b. Retention Period:</h2>
                <ul className="privacy-policy-list">
                    <li>Data is stored for the duration of the competition and associated promotional activities. Personal data that is no longer necessary will be securely deleted thereafter.</li>
                </ul>

                <h2 className="privacy-policy-subtitle">5. Participant Rights</h2>
                <ul className="privacy-policy-list">
                    <li>Access:Request a copy of the personal data held by Blaze.</li>
                    <li>Correction: Request the correction of inaccurate or incomplete personal information.</li>
                    <li>Withdrawal: Withdraw consent for the use of personal data at any time. (Note: This may impact participation in the competition.)</li>
                    <li>Deletion: Request the deletion of personal information, subject to legal obligations or legitimate interests.</li>
                </ul>
                <p>To exercise these rights, participants may contact Blaze at <a href="mailto:support@blazestore.in" className="privacy-policy-link">support@blazestore.in</a>.</p>

                <h2 className="privacy-policy-subtitle">6. Cookies and Tracking Technologies</h2>
                <p>
                    Our website utilizes cookies and similar tracking technologies to enhance the user experience and analyze website traffic. By using the website, you consent to the collection of data via these technologies. Participants may manage their cookie preferences through browser settings.
                </p>

                <h2 className="privacy-policy-subtitle">7. Updates to This Privacy Policy</h2>
                <p>
                    We reserve the right to amend this Privacy Policy at our sole discretion. Significant changes will be communicated via email or an announcement on the official website. Continued participation in the competition after updates constitutes acceptance of the revised terms.
                </p>

                <h2 className="privacy-policy-subtitle">8. Contact Information</h2>
                <p>
                    For any inquiries, concerns, or data-related requests, please contact us via:
                </p>
                <ul className="privacy-policy-list">
                    <li>Email: <a href="mailto:support@blazestore.in" className="privacy-policy-link">support@blazestore.in</a></li>
                    <li>Website: <a href="https://blazestore.in" className="privacy-policy-link">blazestore.in</a></li>
                </ul>
                <p>
                    By participating in the Blaze Digital Art Competition, you confirm that you have read, understood, and agreed to the terms outlined in this Privacy Policy.
                </p>
            </div>
        </div>
    );
};

export default PrivacyPolicy;
