import React, { useState, useEffect } from 'react';
import './Popup.css';

const Popup = () => {
  const [isVisible, setIsVisible] = useState(true);

  useEffect(() => {
    setIsVisible(true); // Show popup on load
  }, []);

  const closePopup = () => {
    setIsVisible(false); // Hide the popup
  };

  const downloadPdf = () => {
    const pdfUrl = '/clanfest digital artcompetition.pdf'; // Ensure this is accessible over HTTPS

    fetch(pdfUrl, { method: 'GET' })
      .then((response) => {
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        return response.blob(); // Convert response to a blob
      })
      .then((blob) => {
        const blobUrl = window.URL.createObjectURL(blob); // Create blob URL
        const link = document.createElement('a');
        link.href = blobUrl;
        link.download = 'clanfest digital artcompetition.pdf'; // File name for download
        document.body.appendChild(link);
        link.click(); // Simulate click
        document.body.removeChild(link); // Clean up
      })
      .catch((error) => {
        console.error('Error downloading the file:', error);
      });
  };

  if (!isVisible) return null;

  return (
    <div className="popup-overlay">
      

      <div className="popup">
       <div className='buttoncont' onClick={closePopup}>
       <button className="close-btn" >×</button>
       </div>
       <a href={require('../../assets/clanfest digital artcompetition.pdf')} download="clanfest digital art competition">
        <img src="/popup.png" alt="Popup" /* onClick={downloadPdf}  *//>
        </a>
      </div>
     
    </div>
  );
};

export default Popup;
