import React, { useEffect, useState } from "react";
import "./WinnerDesigns.css";
import { getData, postData, serverURL } from "../../../services/NodeServices";
import { toast } from "react-toastify";

export default function WinnersDesigns() {
  const [designType, setDesignType] = useState("GYM");
  const [designlist, setDesignList] = useState([]);
  const [showImage, setShowImage] = useState(false);
  const [image, setImage] = useState([]);
  const [designCounts, setDesignCounts] = useState({});
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 8; // Number of images per page

  const getAllDesigns = async () => {
    try {
      const result = await getData("api/design/alldesigns");
      if (result.status) {
        const preselectOnly = result.data.filter((item) => item.winner == true);
        setDesignList(preselectOnly);

        // Calculate counts
        const counts = preselectOnly.reduce((acc, item) => {
          acc[item.category] = (acc[item.category] || 0) + 1;
          return acc;
        }, {});
        setDesignCounts(counts);
      }
    } catch (error) {
      console.error("Error fetching designs:", error);
    }
  };

  useEffect(() => {
    getAllDesigns();
  }, []);

  const handleImageClick = (img) => {
    setShowImage(true);
    setImage(img);
  };

  const handlePageChange = (newPage) => {
    setCurrentPage(newPage);
  };

  // Pagination logic
  const filteredDesigns = designType === "All" ? designlist : designlist.filter(
    (item) => item.category === designType
  );
  const totalPages = Math.ceil(filteredDesigns.length / itemsPerPage);
  const paginatedDesigns = filteredDesigns.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );
  const handlePreSelect=async(item)=>{
    let winner = ''
    if(item.winner==true){
        winner=false
    }else{
        winner=true
    }
    try{
      const result = await postData('api/design/updatewinners',{designid:item.designid,winner})
      console.log(result)
      if(result.status){
        getAllDesigns()
        toast.success('status updated')
      }
    }catch{

    }

  }

  return (
    <div className="mysubmissions__container">
      <div className="mysubmissions__header">Winner Designs</div>
      <div className="form-group">
        <label htmlFor="designType">Select Design Type</label>
        <div className="selectdesigntype">
        <p
            style={{
              backgroundColor: designType === "All" ? "#2f2960" : "#fff",
              color: designType === "All" ? "#fff" : "#000", // Text color change
            }}
            onClick={() => {
              setDesignType("All");
              setCurrentPage(1); // Reset to first page
            }}
          >
            All ({designlist.length})
          </p>
          <p
            style={{
              backgroundColor: designType === "GYM" ? "#2f2960" : "#fff",
              color: designType === "GYM" ? "#fff" : "#000", // Text color change
            }}
            onClick={() => {
              setDesignType("GYM");
              setCurrentPage(1);
            }}
          >
            GYM ({designCounts["GYM"] || 0})
          </p>
          <p
            style={{
              backgroundColor: designType === "Music" ? "#2f2960" : "#fff",
              color: designType === "Music" ? "#fff" : "#000", // Text color change
            }}
            onClick={() => {
              setDesignType("Music");
              setCurrentPage(1);
            }}
          >
            Music ({designCounts["Music"] || 0})
          </p>
          <p
            style={{
              backgroundColor: designType === "Sustainability" ? "#2f2960" : "#fff",
              color: designType === "Sustainability" ? "#fff" : "#000", // Text color change
            }}
            onClick={() => {
              setDesignType("Sustainability");
              setCurrentPage(1);
            }}
          >
            Sustainability ({designCounts["Sustainability"] || 0})
          </p>
          <p
            style={{
              backgroundColor: designType === "Gaming" ? "#2f2960" : "#fff",
              color: designType === "Gaming" ? "#fff" : "#000", // Text color change
            }}
            onClick={() => {
              setDesignType("Gaming");
              setCurrentPage(1);
            }}
          >
            Gaming ({designCounts["Gaming"] || 0})
          </p>
          <p
            style={{
              backgroundColor: designType === "Travel" ? "#2f2960" : "#fff",
              color: designType === "Travel" ? "#fff" : "#000", // Text color change
            }}
            onClick={() => {
              setDesignType("Travel");
              setCurrentPage(1);
            }}
          >
            Travel ({designCounts["Travel"] || 0})
          </p>
          <p
            style={{
              backgroundColor: designType === "Social Thoughts" ? "#2f2960" : "#fff",
              color: designType === "Social Thoughts" ? "#fff" : "#000", // Text color change
            }}
            onClick={() => {
              setDesignType("Social Thoughts");
              setCurrentPage(1);
            }}
          >
            Social Thoughts ({designCounts["Social Thoughts"] || 0})
          </p>
        </div>
      </div>

      <div className="mysubmissions__images">
        {paginatedDesigns.map((item) => {
          let images = item.images.split(",");
          return (
            <div className="mysubmissions__image22" key={item.id}>
              <p>{item.userid}</p>
             {/*  <button  style={{backgroundColor:item.winner==true?'#459a52':'#2f2960'}}>{item.winner==true?'winner':''}</button> */}
              <svg onClick={()=>{handlePreSelect(item)}} width="16" height="18" viewBox="0 0 16 18" fill="none" xmlns="http://www.w3.org/2000/svg">
<path d="M3 18C2.45 18 1.97917 17.8042 1.5875 17.4125C1.19583 17.0208 1 16.55 1 16V3H0V1H5V0H11V1H16V3H15V16C15 16.55 14.8042 17.0208 14.4125 17.4125C14.0208 17.8042 13.55 18 13 18H3ZM13 3H3V16H13V3ZM5 14H7V5H5V14ZM9 14H11V5H9V14Z" fill="#1D1B20"/>
</svg>

              <img
                src={`${serverURL}uploads/${images[0]}`}
                alt={item.designType}
                onClick={() => {
                  handleImageClick(images[0]);
                }}
              />
            </div>
          );
        })}
      </div>

      {/* Pagination Controls */}
      <div className="pagination">
        {Array.from({ length: totalPages }, (_, index) => index + 1).map(
          (page) => (
            <button
              key={page}
              className={`pagination__button pgb${
                page === currentPage ? "active" : "s"
              }`}
              onClick={() => handlePageChange(page)}
            >
              {page}
            </button>
          )
        )}
      </div>

      {showImage && (
        <div className="showimage">
          <img
            src="/xicon.png"
            onClick={() => {
              setShowImage(false);
            }}
            alt="Close"
          />
          <div className="showimagemain">
            <img src={`${serverURL}uploads/${image}`} alt="Selected" />
          </div>
        </div>
      )}
    </div>
  );
}
