import React, { useState, useEffect } from "react";
import "./AllDesignsByuser.css";
import { toast } from "react-toastify";
import { postData, serverURL } from "../../../services/NodeServices";

export default function AllDesignsByUser({ user }) {
  const [designType, setDesignType] = useState("All"); // Default to "All"
  const [designList, setDesignList] = useState([]);

  const getAllDesigns = async () => {
    let body = {
      userid: user.userid,
    };

    try {
      const result = await postData("api/design/byid", body);
      if (result.status) {
        setDesignList(result.data);
        toast.success("Fetched designs successfully");
      }
    } catch (error) {
      toast.error("Failed to fetch designs");
    }
  };

  useEffect(() => {
    getAllDesigns();
  }, []);

  // Calculate counts for each category
  const categoryCounts = designList.reduce((acc, item) => {
    acc[item.category] = (acc[item.category] || 0) + 1;
    return acc;
  }, {});

  return (
    <div className="mysubmissions__container">
      <div className="mysubmissions__header2">User Submissions</div>
      <div className="form-group">
        <label htmlFor="designType">Select Design Type</label>
        <div className="selectdesigntype">
          {/* Add "All" as the first option */}
          <p
            style={{
              backgroundColor: designType === "All" ? "#2f2960" : "#fff",color:designType=='All'?'#fff':'#553'
            }}
            onClick={() => setDesignType("All")}
          >
            All ({designList.length}) {/* Show count for all */}
          </p>
          {[
            "GYM",
            "Music",
            "Sustainability",
            "Gaming",
            "Travel",
            "Social Thoughts",
          ].map((type) => (
            <p
              key={type}
              style={{
                backgroundColor: designType === type ? "#2f2960" : "#fff",color:designType==type?'#fff':'#553'
              }}
              onClick={() => setDesignType(type)}
            >
              {type} ({categoryCounts[type] || 0}) {/* Display count */}
            </p>
          ))}
        </div>
      </div>

      <div className="mysubmissions__images">
        {designList
          .filter(
            (item) =>
              designType === "All" || item.category === designType // Include all if "All" is selected
          )
          .map((item) => {
            let images = item.images.split(",");
            return (
              <div className="mysubmissions__image" key={item.id}>
                <img
                  src={`${serverURL}uploads/${images[0]}`}
                  alt={item.designType}
                />
              </div>
            );
          })}
      </div>
    </div>
  );
}
