import React, { useState, useEffect } from "react";
import { postData, getData } from "../../../../services/NodeServices";
import "./StatsPage.css";

export default function Statspage() {
  const [designs, setDesigns] = useState([]);
  const [designsByCategory, setDesignsByCategory] = useState([]);
  const [users, setUsers] = useState([]);
  const [usersByAgeGroup, setUsersByAgeGroup] = useState([]);

  useEffect(() => {
    getDesignsData();
    getUserData();
  }, []);

  async function getDesignsData() {
    try {
      const result = await getData("api/design/alldesigns");
      if (result.status) {
        setDesigns(result.data);
        groupDesignsByCategory(result.data);
      }
    } catch (error) {
      console.error("Failed to fetch design data:", error);
    }
  }

  function groupDesignsByCategory(designs) {
    const categoryCount = designs.reduce((acc, design) => {
      const category = design.category || "Uncategorized"; // Fallback if no category
      acc[category] = (acc[category] || 0) + 1;
      return acc;
    }, {});

    const groupedData = Object.entries(categoryCount).map(([category, count]) => ({
      category,
      number: count,
    }));

    setDesignsByCategory(groupedData);
  }

  async function getUserData() {
    try {
      const result = await getData("api/users/all");
      if (result.status) {
        setUsers(result.data);
        groupUsersByAge(result.data);
      }
    } catch (error) {
      console.error("Failed to fetch user data:", error);
    }
  }

  function groupUsersByAge(users) {
    const ageGroups = {
      "8-12": 0,
      "13-17": 0,
      "18+": 0,
    };
       console.log(users)
    users.forEach((user) => {
      const age = user.group; // Assuming `age` is available in the user data
      if (age=='8-12') {
        ageGroups["8-12"] += 1;
      } else if (age =='13-17') {
        ageGroups["13-17"] += 1;
      } else {
        ageGroups["18+"] += 1;
      }

    });

    const groupedData = Object.entries(ageGroups).map(([group, count]) => ({
      group,
      number: count,
    }));

    setUsersByAgeGroup(groupedData);
  }

  return (
    <div className="statspage__cont">
      <div className="statuspage__header">
        <div className="statspage__designsnumber">
          {designs.length} Designs Submitted
        </div>

        <div className="designsbycategory">
          {designsByCategory.map((item, index) => (
            <div className="designsbycategory__item" key={index}>
              <p>{item.category}</p> <p>{item.number}</p>
            </div>
          ))}
        </div>

        <div className="statspage__usersnumber">
          {users.length} Users Registered
        </div>

        <div className="usersbyagegroup">
          {usersByAgeGroup.map((item, index) => (
            <div className="usersbyagegroup__item" key={index}>
              <p>{item.group}</p> <p>{item.number}</p>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
