import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import "./UserProfileView.css";

import AllDesignsByUser from "./AllDesignsByUser";
import { postData } from "../../../services/NodeServices";

const UserDetails = () => {
  const [user, setUser] = useState(null);
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  const userid = queryParams.get("userid");

  useEffect(() => {
    if (userid) {
      getUserData();
    }
  }, [userid]);

  const getUserData = async () => {
    let body = {
      userid: userid,
    };
    try {
      const result = await postData("api/users/byid", body);
      if (result.status) {
        setUser(result.data);
      }
    } catch (error) {
      console.error("Error fetching user data:", error);
    }
  };

  if (!user) {
    return <div className="user-details__loading">Loading...</div>;
  }

  return (
    <div className="user-details__container">
      <h2 className="user-details__heading">User Details</h2>
      <div className="user-details__info">
        <div className="user-details__row">
          <label className="user-details__label">ID:</label>
          <span className="user-details__value">{user.userid}</span>
        </div>
        <div className="user-details__row">
          <label className="user-details__label">Name:</label>
          <span className="user-details__value">
            {user.firstname} {user.lastname}
          </span>
        </div>
        <div className="user-details__row">
          <label className="user-details__label">Email:</label>
          <span className="user-details__value">{user.email}</span>
        </div>
   {/*      <div className="user-details__row">
          <label className="user-details__label">Username:</label>
          <span className="user-details__value">{user.username}</span>
        </div>
        <div className="user-details__row">
          <label className="user-details__label">Mobile:</label>
          <span className="user-details__value">{user.mobile}</span>
        </div>
        <div className="user-details__row">
          <label className="user-details__label">Country:</label>
          <span className="user-details__value">{user.country}</span>
        </div>
        <div className="user-details__row">
          <label className="user-details__label">State:</label>
          <span className="user-details__value">{user.state}</span>
        </div>
        <div className="user-details__row">
          <label className="user-details__label">Street:</label>
          <span className="user-details__value">{user.street}</span>
        </div>
        <div className="user-details__row">
          <label className="user-details__label">Gender:</label>
          <span className="user-details__value">{user.gender}</span>
        </div>
        <div className="user-details__row">
          <label className="user-details__label">Status:</label>
          <span className="user-details__value">{user.status}</span>
        </div> */}
        <div className="user-details__row">
          <label className="user-details__label">Age</label>
          <span className="user-details__value">{user.group}</span>
        </div>
       
      </div>

      <AllDesignsByUser user={user}/>
    </div>
  );
};

export default UserDetails;
