import React, { useContext } from 'react';
import { Navigate, Outlet } from 'react-router-dom';
import { UserContext } from '../../context/UserContext';
import NotFound from '../../components/NotFound/NotFound';



const AdminRoutesMain = ({ children, adminOnly = false }) => {
  const { isloggedIn, user } = useContext(UserContext);

  console.log(isloggedIn,user)

  // Check if the user is logged in and, optionally, if they're an admin
  if (!isloggedIn || (user?.role != 'admin')) {
    return <NotFound />;
  }

  // Render children or nested routes
  return children ? children : <NotFound />;
};

export default AdminRoutesMain;
