import logo from './logo.svg';
import './App.css';

import Login from './components/Login/Login';
import SignUp from './components/Signup/SignUp';
import ImageUploadComp from './components/SubmitDesign/SubmitDesign';
import Navbar from './components/Navbar/Navbar';
import SubmitDesignPage from './Pages/SubmitDesignPage';
import { BrowserRouter as Router,Routes,Route } from 'react-router-dom';
import Admin from './Pages/admin/AdminPanel';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import UserPanel from './Pages/userpanel/UserPanel';
import { UserProvider } from './context/UserContext';
import { useContext } from 'react';
import { UserContext } from './context/UserContext';
import { useEffect } from 'react';
import ProtectedRoute from './Pages/ProtectedRoute/ProtectedRoute';
import NotFound from './components/NotFound/NotFound';
import AdminRoutesMain from './Pages/ProtectedRoute/AdminMainRoute';
import WhyParticipate from './Pages/WhyParticipate/WhyParticipate';
import Guidelines from './Pages/Guidelines/Guidelines';
import Footer from './components/Footer/Footer';
import JudgingCriteria from './Pages/Prizes/Prizes';
import ContactPage from './Pages/ContactPage/Contactpage';
import Faq from './Pages/Faq/Faq';
import PrivacyPolicy from './Pages/PrivacyPolicy/PrivacyPolicy';
import Terms from './Pages/Terms/Terms';
import Cancel from './Pages/Cancel/Cancel';
import AboutUs from './Pages/Policies/About us';
import PricingPolicy from './Pages/Policies/Pricing Policy';
import ShippingPolicy from './Pages/Policies/ShippingPolicy';
const AppContent=()=>{

 /*  localStorage.clear() */

  const { setUser, setisLoggedIn,isloggedIn } = useContext(UserContext); // Correct usage of UserContext inside the provider

  useEffect(() => {
    // Check local storage asynchronously after page load
    const storedUser = localStorage.getItem('user');
    if (storedUser) {
      const userData = JSON.parse(storedUser);
      setUser(userData); // Set user data from local storage
      setisLoggedIn(true); // Update logged-in state
    }
  }, [setUser, setisLoggedIn]); 
  useEffect(() => {
    // Check local storage asynchronously after page load
    const storedUser = localStorage.getItem('user');
    if (storedUser) {
      const userData = JSON.parse(storedUser);
      setUser(userData); // Set user data from local storage
      setisLoggedIn(true); // Update logged-in state
    }
  }, []); 
  const LayoutComp=({child})=>{
    return(
      <>
      <Navbar/>
      {child}
      <Footer/>
      </>
    )

  }
/*   localStorage.clear() */

  return(     <div className="App">
 
    
    
   
    <Router basename='/competition'>
   
     <Routes>
       <Route path='/'  element={<LayoutComp child={<SubmitDesignPage/>}/>}/>
       <Route path='/login'  element={<Login/>}/>
       <Route path='/signup'  element={<SignUp/>}/>
       <Route path='/signup'  element={<SignUp/>}/>
       <Route path='/why-participate'  element={<LayoutComp child={<WhyParticipate/>}/>}/>
       <Route path='/guidelines'  element={<LayoutComp child={<Guidelines/>}/>}/>
       <Route path='/prizes'  element={<LayoutComp child={<JudgingCriteria/>}/>}/>
       <Route path='/contact'  element={<LayoutComp child={<ContactPage/>}/>}/>
       <Route path='/designs'  element={<ImageUploadComp/>}/>
       <Route path='/admin/*'  element={<AdminRoutesMain children={<Admin/>}/>}/>
       <Route path='/user/*'  element={<ProtectedRoute><UserPanel/></ProtectedRoute>}/>
       <Route path='/faq'  element={<LayoutComp child={<Faq/>}/>}/>
       <Route path='/privacy-policy'  element={<LayoutComp child={<PrivacyPolicy/>}/>}/>
       <Route path='/terms-and-conditions'  element={<LayoutComp child={<Terms/>}/>}/>
       <Route path='/cancellation-and-refund'  element={<LayoutComp child={<Cancel/>}/>}/>
       <Route path='/about-us'  element={<LayoutComp child={<AboutUs/>}/>}/>
       <Route path='/pricing-policy'  element={<LayoutComp child={<PricingPolicy/>}/>}/>
       <Route path='/shipping-policy'  element={<LayoutComp child={<ShippingPolicy/>}/>}/>
       <Route path='*' element={<NotFound/>} />
     </Routes>
   
    </Router>
   
    <div className='toastcont'> <ToastContainer /></div>
   
   </div>)
}

function App() {






  return (
   <UserProvider>
    <AppContent/>
   </UserProvider>
  );
}

export default App;
