import React, { useContext, useState, useEffect, useRef } from "react";
import styles from "./Navbar.module.scss";
import { useNavigate } from "react-router-dom";
import { UserContext } from "../../context/UserContext";
import AOS from "aos";
import "aos/dist/aos.css"; 
export default function Navbar() {
  const { isloggedIn, user } = useContext(UserContext);
  const navigate = useNavigate();
  const [mininav, setMiniNav] = useState(false);
  const miniNavRef = useRef(null);

  const handleClickOutside = (event) => {
    if (miniNavRef.current && !miniNavRef.current.contains(event.target)) {
      setMiniNav(false);
    }
  };
  const handleMiniNavClick = (path) => {
    navigate(path); // Navigate to the specified path
    setMiniNav(false); // Close the mini navigation
  };
  useEffect(() => {
    if (mininav) {
      document.addEventListener("mousedown", handleClickOutside);
    } else {
      document.removeEventListener("mousedown", handleClickOutside);
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [mininav]);


  useEffect(() => {
    AOS.init({
      duration: 600, // Duration of the animation in milliseconds
      easing: "ease-in-out", // Easing function
      once: true, // Whether animation should happen only once
    });
  }, []);
  return (<div style={{width:'100%',position:'fixed',zIndex:22,marginBottom:41,zIndex:2331}} > 
    <div className={styles.navbar__cont} data-aos="fade-down"
    data-aos-delay="400" 
    data-aos-duration="600" 
    data-aos-easing="ease-in-out"> 
      <div className={styles.logo} onClick={()=>{navigate('/')}}>Clanfest</div>
      <div className={styles.headers}>
     
        <p onClick={() => navigate("/guidelines")}>Guidelines</p>
        <p onClick={() => navigate("/prizes")}>Prizes</p>
        <p onClick={() => navigate("/why-participate")}>Why Participate</p>
        <p onClick={() => navigate("/contact")}>Contact</p>
        <img src="/menu.png" onClick={() => setMiniNav(true)} />
        <div
          ref={miniNavRef}
          className={`${styles.mininavv} ${mininav ? styles.active : ""}`} style={{transition: 'transform 0.3s ease'}}
          
        > <h6> <div style={{height:41,width:'99%',display:'flex',justifyContent:'flex-end',paddingRight:5,color:'#222',alignItems:'center',backgroundColor:'#fff',borderBottomColor:'rgba(135, 135, 119, 0.3607843137)',color:'#444'}}><img style={{width:21}} onClick={()=>{setMiniNav(false)}} src="/close.png"/></div></h6>
           <h6 onClick={() => handleMiniNavClick("/")}>Home</h6>
             <h6 onClick={() => handleMiniNavClick("/guidelines")}>Guidelines</h6>
            <h6 onClick={() => handleMiniNavClick("/prizes")}>Prizes</h6>
            <h6 onClick={() => handleMiniNavClick("/why-participate")}>
              Why Participate
            </h6>
            <h6 onClick={() => handleMiniNavClick("/contact")}>Contact</h6>

        {isloggedIn ? (
          <h6
            onClick={() =>
              user.role === "user" ? navigate("/user") : navigate("/admin")
            }
          >
            Profile
          </h6>
        ) : (
          <h6 onClick={() => handleMiniNavClick("/login")}>Login</h6>
        )}
          
        </div>
        {isloggedIn ? (
          <p
            onClick={() =>
              user.role === "user" ? navigate("/user") : navigate("/admin")
            }
          >
            Profile
          </p>
        ) : (
          <p onClick={() => navigate("/login")}>Login</p>
        )}
      </div>
    </div>
    </div>
  );
}
