
import { useContext, useState } from 'react'
import './Login.css'
import { postData } from '../../services/NodeServices'
import { toast } from 'react-toastify'
import { useNavigate } from 'react-router-dom'
import { UserContext } from '../../context/UserContext'



export default function Login(){
  const { loginUser,isloggedIn,setisLoggedIn,saveUser } = useContext(UserContext);

  const navigate = useNavigate()

    const [formdata,setFormData]=useState({

        email:'',
        password:'',
     

    })

    
   const handleSubmit=async()=>{
    console.log(formdata)
    if(formdata.email==''){
      toast.error('please enter valid details')
      return
    }
    try{
     const result = await postData('api/users/login',formdata)
     console.log(result)
     if(result.status){
       if(result.redirectUrl){
        setisLoggedIn(true)
        toast.success('Logged in')
        loginUser(result.data)
       localStorage.setItem('user',JSON.stringify(result.data))

        navigate('/admin')
       }else{
        setisLoggedIn(true)
        toast.success('Logged in')
        loginUser(result.data.user)
       localStorage.setItem('user',JSON.stringify(result.data.user))
        navigate('/user')
       }
     }else{
      console.log('error showing')
      toast.error("incorrect email or password");
     
     }

    }catch{
     
    }
   }
     
   


    return(
        <div className='login__cont'>
         {/*  <div className='mainbg'>
           <img src='/Asset 3@2x.png' />
          </div> */}

            <div className='login__header'>
             Login
            </div>
            <div className='login__continner'>
            <div className="form-group">


<label htmlFor="email">Email</label>
  <input
    type="text"
    id="email"
    name="email"
    placeholder="Enter your email address"
    onChange={(e)=>{setFormData({...formdata,[e.target.name]:e.target.value})}}
    required
  />

<label htmlFor="name">Password</label>
  <input
    type="text"
    id="password"
    name="password"
    placeholder="Enter your password"
    onChange={(e)=>{setFormData({...formdata,[e.target.name]:e.target.value})}}
    required
  />



 <button className="inputbutton" onClick={handleSubmit}>Login</button>

</div>

<div className='loginbot'>
 Dont have an account ? <p onClick={()=>{navigate('/signup')}}>Signup</p>
</div>

            </div>

        </div>
    )
}