import React, { useContext, useEffect, useState } from "react";
import "./MyWins.css";
import { getData, postData, serverURL } from "../../services/NodeServices";
import { UserContext } from "../../context/UserContext";
import { toast } from "react-toastify";

export default function MyWins() {
  const [designType, setDesignType] = useState("GYM");
  const [designlist, setDesignList] = useState([]);
  const { user } = useContext(UserContext);

  const getAllDesigns = async () => {
    let user = JSON.parse(localStorage.getItem("user"));
    let body = {
      userid: user.userid,
    };

    try {
      const result = await postData("api/design/byid", body);
      if (result.status) {
        const filteredDesigns = result.data.filter((design) => design.winner === true);
        setDesignList(filteredDesigns);
        console.log(filteredDesigns);
        toast.success("fetched designs successfully");
      }
    } catch {}
  };

  useEffect(function () {
    getAllDesigns();
  }, []);

  // Calculate counts for each category
  const categoryCounts = designlist.reduce((acc, item) => {
    acc[item.category] = (acc[item.category] || 0) + 1;
    return acc;
  }, {});

  return (
    <div className="mysubmissions__container">
      <div className="mysubmissions__header">My Wins</div>
      <div className="form-group">
        <label htmlFor="designType">Select Design Type</label>
        <div className="selectdesigntype">
          {[
            "ALL",
            "GYM",
            "Music",
            "Sustainability",
            "Gaming",
            "Travel",
            "Social Thoughts",
          ].map((type) => (
            <p
              key={type}
              style={{
                backgroundColor: designType === type ? "#1D9E91" : "#fff",
                color: designType === type ? "#fff" : "#000", // Set text color to white when selected
              }}
              onClick={() => setDesignType(type)}
            >
              {type} ({type === "ALL" ? designlist.length : categoryCounts[type] || 0}) {/* Display count */}
            </p>
          ))}
        </div>
      </div>

      <div className="mysubmissions__images">
        {designlist
          .filter((item) => designType === "ALL" || item.category === designType) // Show all if "ALL" is selected
          .map((item) => {
            let images = item.images.split(",");
            return (
              <div className="mysubmissions__image" key={item.id}>
                <img
                  src={`${serverURL}uploads/${images[0]}`}
                  alt={item.designType}
                />
              </div>
            );
          })}
      </div>
    </div>
  );
}
