import { useContext, useState } from "react";
import "./SignUp.css";
import { postData, serverURL } from "../../services/NodeServices";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import { UserContext } from "../../context/UserContext";

export default function SignUp() {
  const navigate = useNavigate();

  const [formdata, setFormData] = useState({
    name: "",
    email: "",
    password: "",
    password2: "",
    from: "",
    group: "",
    fromOther: "",
    number: "",
    volunteer: "",
  });
  const { loginUser, setisLoggedIn } = useContext(UserContext);
  const [errors, setErrors] = useState({});

  const validate = () => {
    console.log('validating now ')
    const newErrors = {};
    let isvalid = true

    if (!formdata.name.trim()){
      newErrors.name = "Name is required.";
      isvalid = false
    }
    if (!formdata.email.trim()) {
      newErrors.email = "Email is required.";
      isvalid = false
    }
    else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formdata.email)){
      newErrors.email = "Enter a valid email address.";
      isvalid =false
    }
      

    if (!formdata.number.trim()) {
      newErrors.number = "Mobile number is required.";
      isvalid = false
    }
    else if (!/^\d{10}$/.test(formdata.number)){
      newErrors.number = "Enter a valid 10-digit mobile number.";
    }
    console.log('current formdata from is ',formdata.from)
   if(formdata.from=='Volunteer'){
    if (!formdata.volunteer.trim()) {
      newErrors.volunteer = "Mobile number is required.";
      isvalid = false
    }
    else if (!/^\d{10}$/.test(formdata.volunteer)){
      newErrors.volunteer = "Enter a valid 10-digit mobile number.";
    }
   }
    

    if (!formdata.password.trim()) {
      newErrors.password = "Password is required.";
      isvalid = false
    }
    if (formdata.password !== formdata.password2){
      newErrors.password2 = "Passwords do not match.";
      isvalid = false
    }
    if (formdata.group==''){
      newErrors.group= "Please slect age group";
      isvalid = false
    }

   console.log('new errors are',newErrors)

    setErrors(newErrors);
    return isvalid;
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formdata, [name]: value });
    setErrors({ ...errors, [name]: "" }); // Clear the error for the current field
  };

  
  /*    
   const handleSubmit=async()=>{
    console.log(formdata)
    try{
     const result = await postData('api/users/signu',formdata)
     console.log(result)
     if(result.status){
          navigate('/')
          toast.success('logged in')
     }
    }catch{

    }
   } */
  const loadRazorpayScript = () => {
    return new Promise((resolve, reject) => {
      const script = document.createElement("script");
      script.src = "https://checkout.razorpay.com/v1/checkout.js";
      script.onload = () => resolve(true);
      script.onerror = () =>
        reject(new Error("Failed to load Razorpay script"));
      document.body.appendChild(script);
    });
  };
  const [error, setError] = useState("");

  const handleSubmit = async () => {


    if(!validate()){
      return
    }

    setError("");

    loadRazorpayScript();

    const response = await fetch(`${serverURL}create-order`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ amount: 590 }),
    });

    const order = await response.json();

    const options = {
      key: "rzp_test_nt89OO93hs7lOa",
      amount: order.amount,
      currency: order.currency,
      name: "User Registration",
      description: "Payment for submitting design",
      order_id: order.id,
      handler: async (paymentResponse) => {
        console.log(paymentResponse);

        const verifyResponse = await fetch(`${serverURL}verify-payment2`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            payment_id: paymentResponse.razorpay_payment_id,
            order_id: paymentResponse.razorpay_order_id,
            signature: paymentResponse.razorpay_signature,
            name: formdata.name,
            email: formdata.email,
            password: formdata.password,
            from: formdata.from == "Other" ? formdata.fromOther : formdata.from,
            volunteer: formdata.volunteer,
            agegroup: formdata.group,
            mobile: formdata.number,
          }),
        });

        const verificationResult = await verifyResponse.json();
        console.log(verificationResult);

        if (verificationResult.status) {
          loginUser(verificationResult.user);
          localStorage.setItem("user", JSON.stringify(verificationResult.user));
          setisLoggedIn(false);

          setTimeout(() => {
            navigate("/user");
          }, 511);
        } else {
          setError("Payment verification failed. Please try again.");
        }
      },
      prefill: {
        name: "User Name",
        email: "user@example.com",
      },
      theme: {
        color: "#3399cc",
      },
    };

    const razorpay = new window.Razorpay(options);
    razorpay.open();
  };

  return (
    <div className="login__cont">
      {/*    <div className='mainbg'>
           <img src='/Asset 3@2x.png' />
          </div> */}

      <div className="login__header">Sign up</div>
      <div className="login__continner">
        <div className="form-group">
           <label htmlFor="name">Name</label>
          <input
            type="text"
            id="name"
            name="name"
            placeholder="Enter your name"
            onChange={handleChange}
            onKeyPress={(e) => {
              // Allow only alphabets and spaces
              const regex = /^[a-zA-Z\s]*$/;
              if (!regex.test(e.key)) {
                e.preventDefault();
              }
            }}
          />
          {errors.name && <label className="error-label">{errors.name}</label>}

          <label htmlFor="email">Email</label>
          <input
            type="text"
            id="email"
            name="email"
            placeholder="Enter your email address"
            onChange={handleChange}

          
            onKeyPress={(e) => {
              const allowedCharacters = /^[a-zA-Z0-9@._-]$/; // Valid email characters
              const input = e.target.value;
          
              if (!allowedCharacters.test(e.key)) {
                e.preventDefault(); 
              }
              if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formdata.email)){
                
                setErrors({ ...errors,email:'Enter a valid email address' });
              }
            }}
          />
          {errors.email && <label className="error-label">{errors.email}</label>}


         <label htmlFor="number">Mobile Number</label>
          <input
            type="text"
            id="number"
            name="number"
            placeholder="Enter your mobile no."
            onChange={handleChange}
            onKeyPress={(e) => {
              const input = e.target.value;
          
              // Allow only numeric characters and limit to 10 digits
              if (!/^[0-9]$/.test(e.key) || input.length >= 10) {
                e.preventDefault(); // Prevent non-numeric input and extra characters
              }
            }}
          />
          {errors.number && <label className="error-label">{errors.number}</label>}
          
          <label htmlFor="from">How did you hear about us?</label>
          <select
            id="from"
            name="from"
            onChange={(e) => {
              setFormData({ ...formdata, [e.target.name]: e.target.value });
            }}
            required
          >
            <option value="">Select Source</option>
            <option value="Social Media">Social Media</option>

            <option value="Other">Other</option>
            <option value="Volunteer">Volunteer</option>
          </select>

          {formdata.from === "Other" && (
            <>
              <div style={{ width: "99%", height: "1.1rem" }}></div>
              <input
                type="text"
                id="fromOther"
                name="fromOther"
                placeholder="Please specify"
                value={formdata.fromOther || ""} // Maintain input value for "Other"
                onChange={
                  (e) => setFormData({ ...formdata, fromOther: e.target.value }) // Update "Other" input value
                }
                onKeyPress={(e) => {
                  // Allow only numeric characters (0-9)
                  if (!/[a-zA-Z]/.test(e.key)) {
                    e.preventDefault(); // Prevent any non-numeric characters
                  }
                }}
                required
              />
            </>
          )}
  {formdata.from === "Volunteer" && (
            <>
              <div style={{ width: "99%", height: "1.1rem" }}></div>
              <input
                type="text"
                id="volunteer"
                name="volunteer"
                placeholder=" Volunteer Mobile no"
                value={formdata.volunteer} // Maintain input value for "Other"
                
                onChange={
                  (e) => setFormData({ ...formdata, volunteer : e.target.value })

                }
                onKeyPress={(e) => {
                  const input = e.target.value;
                  // Allow only numeric characters (0-9)
                  if (!/^[0-9]$/.test(e.key)|| input.length >= 10) {
                    e.preventDefault(); // Prevent any non-numeric characters
                  }
                }}
                required
              />
              {errors.volunteer && <label className="error-label">{errors.volunteer}</label>}
            </>
          )}
          <label htmlFor="ageGroup">Age Group</label>
          <select
            id="group"
            name="group"
            onChange={(e) => {
              setFormData({ ...formdata, [e.target.name]: e.target.value });
            }}
            required
          >
            <option value="">Select Age Group</option>
            <option value="8-12">8-12</option>
            <option value="13-17">13-17</option>
            <option value="18+">18+</option>
          </select>
          {errors.group && <label className="error-label">{errors.group}</label>}

          <label htmlFor="password">Password</label>
          <input
            type="password"
            id="password"
            name="password"
            placeholder="Enter your password"
            onChange={handleChange}
          />
          {errors.password && <label className="error-label">{errors.password}</label>}

          <label htmlFor="password2">Reenter your password</label>
          <input
            type="password"
            id="password2"
            name="password2"
            placeholder="Enter your password again"
            onChange={handleChange}
          />
          {errors.password2 && <label className="error-label">{errors.password2}</label>}

          <button className="inputbutton" onClick={handleSubmit}>
            Pay 500/- +18% GST
          </button>
        </div>
        <div className="loginbot">
          Already have an account ?{" "}
          <p
            onClick={() => {
              navigate("/login");
            }}
          >
            Login
          </p>
        </div>
      </div>
    </div>
  );
}
