import React, { useEffect } from "react";
import "./Faq.css"; // Make sure to import your CSS file

export default function Faq() {


    useEffect(() => {
        window.scrollTo(0, 0); // Scrolls to the top of the page
      }, []);
  const faqList = [
    { question: "What is the Blaze Digital Art Competition?", answer: `The Blaze Digital Art Competition is a national event where artists can showcase
their talent, gain recognition, and win amazing prizes, including cash rewards. It's a
platform to elevate your career and open new opportunities.
` },
    { question: "Can I collaborate with a friend?", answer: "Submissions must be individual." },
    { question: `What format is required for digital submissions?`, answer: "JPEG or PDF format, with a minimum resolution of 300 DPI." },
    { question: `How will the winners be selected?`, answer: "JPEG or PDF format, with a minimum resolution of 300 DPI." },
    { question: `What format is required for digital submissions?`, answer: `Winners will be chosen based on creativity, originality, and adherence to the
theme. A panel of experts will evaluate the submissions.` },
    { question: `How will I know if my submission is selected?`, answer: `Selected participants will be visible on website dashboard of clanfest and
announced on the official Blaze platforms (social media).
` },
    { question: `Will my artwork be showcased publicly?
`, answer:`Yes, selected artworks will be showcased to millions of viewers, giving
participants national exposure.
`},
    { question: `When is the submission deadline?`, answer: `The deadline will be provided in the competition announcement. Make sure to submit your entry on time to avoid disqualification.` },

    { question: `8. How will I know if my submission is selected?
`, answer: `Selected participants will be visible on website dashboard of clanfest and announced on the official Blaze platforms (social media).` },
    // Add more questions and answers as needed
  ];

  return (
    <div className="faq-container">
      
     <div className="faqinner">
     <h1 className="faq-title">Frequently Asked Questions</h1>
      <ul className="faq-list">
    
        {faqList.map((faq, index) => (
          <li key={index} className="faq-item">
             <p>{faq.question}</p>
            <p>{faq.answer}</p>
          </li>
        ))}
      </ul>
     </div>
    </div>
  );
}
