import React, { useEffect, useState } from "react";
import "./AllDesigns2.css";
import { getData, postData, serverURL } from "../../../services/NodeServices";

export default function AllDesigns2() {
  const [designType, setDesignType] = useState("All"); // Default to "All"
  const [designlist, setDesignList] = useState([]);
  const [showImage, setShowImage] = useState(false);
  const [image, setImage] = useState([]);
  const [designCounts, setDesignCounts] = useState({});
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 8; // Number of images per page

  const getAllDesigns = async () => {
    try {
      const result = await getData("api/design/alldesigns");
      if (result.status) {
        setDesignList(result.data);

        // Calculate counts
        const counts = result.data.reduce((acc, item) => {
          acc[item.category] = (acc[item.category] || 0) + 1;
          return acc;
        }, {});
        setDesignCounts(counts);
      }
    } catch (error) {
      console.error("Error fetching designs:", error);
    }
  };

  useEffect(() => {
    getAllDesigns();
  }, []);

  const handleImageClick = (img) => {
    setShowImage(true);
    setImage(img);
  };

  const handlePageChange = (newPage) => {
    setCurrentPage(newPage);
  };

  // Pagination logic
  const filteredDesigns = designType === "All" 
    ? designlist 
    : designlist.filter((item) => item.category === designType);

  const totalPages = Math.ceil(filteredDesigns.length / itemsPerPage);
  const paginatedDesigns = filteredDesigns.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  const handlePreSelect = async (item) => {
    let preselect = item.preselect==true ? false : true;
    
    try {
      const result = await postData("api/design/updatepreselect", {
        designid: item.designid,
        preselect,
      });
      console.log(result);
      if (result.status) {
        getAllDesigns();
      }
    } catch {
      console.error("Failed to update preselect");
    }
  };

  return (
    <div className="mysubmissions__container">
      <div className="mysubmissions__header">All Designs</div>
      <div className="form-group">
        <label htmlFor="designType">Select Design Type</label>
        <div className="selectdesigntype">
          {/* Add "All" as the first option */}
          <p
            style={{
              backgroundColor: designType === "All" ? "#2f2960" : "#fff",color:designType=='All'?'#fff':'#333'
            }}
            onClick={() => {
              setDesignType("All");
              setCurrentPage(1); // Reset to first page
            }}
          >
            All ({designlist.length}) {/* Show count for all */}
          </p>
          <p
            style={{
              backgroundColor: designType === "GYM" ? "#2f2960" : "#fff",color:designType=='GYM'?'#fff':'#333'
            }}
            onClick={() => {
              setDesignType("GYM");
              setCurrentPage(1); // Reset to first page
            }}
          >
            GYM ({designCounts["GYM"] || 0})
          </p>
          <p
            style={{
              backgroundColor: designType === "Music" ? "#2f2960" : "#fff",color:designType=='Music'?'#fff':'#333'
            }}
            onClick={() => {
              setDesignType("Music");
              setCurrentPage(1);
            }}
          >
            Music ({designCounts["Music"] || 0})
          </p>
          <p
            style={{
              backgroundColor:
                designType === "Sustainability" ? "#2f2960" : "#fff",color:designType=='Sustainability'?'#fff':'#333'
            }}
            onClick={() => {
              setDesignType("Sustainability");
              setCurrentPage(1);
            }}
          >
            Sustainability ({designCounts["Sustainability"] || 0})
          </p>
          <p
            style={{
              backgroundColor: designType === "Gaming" ? "#2f2960" : "#fff",color:designType=='Gaming'?'#fff':'#333'
            }}
            onClick={() => {
              setDesignType("Gaming");
              setCurrentPage(1);
            }}
          >
            Gaming ({designCounts["Gaming"] || 0})
          </p>
          <p
            style={{
              backgroundColor: designType === "Travel" ? "#2f2960" : "#fff",color:designType=='Travel'?'#fff':'#333'
            }}
            onClick={() => {
              setDesignType("Travel");
              setCurrentPage(1);
            }}
          >
            Travel ({designCounts["Travel"] || 0})
          </p>
          <p
            style={{
              backgroundColor:
                designType === "Social Thoughts" ? "#2f2960" : "#fff",color:designType=='Social Thoughts'?'#fff':'#333'
            }}
            onClick={() => {
              setDesignType("Social Thoughts");
              setCurrentPage(1);
            }}
          >
            Social Thoughts ({designCounts["Social Thoughts"] || 0})
          </p>
        </div>
      </div>

      <div className="mysubmissions__images">
        {paginatedDesigns.map((item) => {
          let images = item.images.split(",");
          return (
            <div className="mysubmissions__image2" key={item.id}>
              <p>{item.userid}</p>
              <button
                onClick={() => {
                  handlePreSelect(item);
                }}
                style={{
                  backgroundColor: item.preselect == true ? "#459a52" : "#2f2960",
                }}
              >
                {item.preselect == true ? "Preselected" : "Preselect"}
              </button>
              <img
                src={`${serverURL}uploads/${images[0]}`}
                alt={item.designType}
                onClick={() => {
                  handleImageClick(images[0]);
                }}
              />
            </div>
          );
        })}
      </div>

      {/* Pagination Controls */}
      <div className="pagination">
        {Array.from({ length: totalPages }, (_, index) => index + 1).map(
          (page) => (
            <button
              key={page}
              className={`pagination__button pgb${
                page === currentPage ? "active" : "s"
              }`}
              onClick={() => handlePageChange(page)}
            >
              {page}
            </button>
          )
        )}
      </div>

      {showImage && (
        <div className="showimage">
          <img
            src="/xicon.png"
            onClick={() => {
              setShowImage(false);
            }}
            alt="Close"
          />
          <div className="showimagemain">
            <img src={`${serverURL}uploads/${image}`} alt="Selected" />
          </div>
        </div>
      )}
    </div>
  );
}
