import React, { useContext } from "react";
import { useEffect,useState } from "react";
import './Dashboard.css'
import { Route, Routes } from "react-router-dom";
import AllUsers from "./AllUsers";
import AllDesigns from "./AllDesigns";
import { useNavigate } from "react-router-dom";
import Statspage from "./StatsPage/StatsPage";
import AllDesigns2 from "./AllDesigns2";
import PreselectedDesigns from "./PreSelectedDesigns";
import SelectedDesigns from "./SelectedDesigns";
import WinnersDesigns from "./WinnerDesigns";
import { UserContext } from "../../../context/UserContext";
import UserDetails from "./UserProfileView";


export default function Dashboard(){

   const navigate = useNavigate()
   const { user,setisLoggedIn,setUser} = useContext(UserContext);
  
   const handleLogOut=()=>{
  
        localStorage.clear()
        setisLoggedIn(false)
        setUser(null)
        navigate('/')
  
   }
   const handleNavLinkClick = (link) => {
    
    setMiniNav(false)
     
     navigate(link)
  };



   const [miniNav,setMiniNav]=useState(false)
    return(
        <div className="dashboard__container">
              <div className="dashboard__leftnav">
              <img src="/menu.png" onClick={() => setMiniNav(!miniNav)} />
           </div>
            
            <div className="dashboard__leftpanel">
              
                <div className="dashboard__header">
                    Clanfest
                </div>
                <div className="dashboard__header2">
                {user?.firstname}
                </div>
                <div className="dashboard__header2">
                {user?.email}
                </div>
                <div className="dashboard__links">
                <div className="linkk" onClick={() => { handleNavLinkClick('/admin') }}>
            <p>Home</p>
          </div>
          <div className="linkk" onClick={() => { handleNavLinkClick('/admin/allusers')}}>
            <p>All Users</p>
          </div>
          <div className="linkk" onClick={() => {handleNavLinkClick('/admin/alldesigns') }}>
            <p>All Designs</p>
          </div>
          <div className="linkk" onClick={() => { handleNavLinkClick('/admin/preselect') }}>
            <p>Pre Selected</p>
          </div>
          <div className="linkk" onClick={() => { handleNavLinkClick('/admin/select') }}>
            <p>Selected</p>
          </div>
          <div className="linkk" onClick={() => { handleNavLinkClick('/admin/winners') }}>
            <p>Winners</p>
          </div>
                   <div className="linkk logoutlink" onClick={handleLogOut}>
                    <p>Log out</p>
                   </div>
                </div>

            </div>
            <div className="dashboard__leftpanel2" style={{transform:!miniNav?'translateX(-111%)':'',transition: 'transform 0.3s ease',}}>
              
              <div className="dashboard__header">
                  Clanfest
              </div>
              <div className="dashboard__header2">
              {user?.firstname}
              </div>
              <div className="dashboard__header2">
              {user?.email}
              </div>
              <div className="dashboard__links">
              <div className="linkk" onClick={() => { handleNavLinkClick('/admin') }}>
            <p>Home</p>
          </div>
          <div className="linkk" onClick={() => { handleNavLinkClick('/admin/allusers')}}>
            <p>All Users</p>
          </div>
          <div className="linkk" onClick={() => {handleNavLinkClick('/admin/alldesigns') }}>
            <p>All Designs</p>
          </div>
          <div className="linkk" onClick={() => { handleNavLinkClick('/admin/preselect') }}>
            <p>Pre Selected</p>
          </div>
          <div className="linkk" onClick={() => { handleNavLinkClick('/admin/select') }}>
            <p>Selected</p>
          </div>
          <div className="linkk" onClick={() => { handleNavLinkClick('/admin/winners') }}>
            <p>Winners</p>
          </div>
                 <div className="linkk logoutlink" onClick={handleLogOut}>
                  <p>Log out</p>
                 </div>
              </div>

          </div>

            <div className="dasboard__rpanel">
                <Routes>
                
                   <Route  path='/allusers' element={<AllUsers/>}/>
                   <Route  path='/alldesigns' element={<AllDesigns2/>}/>
                   <Route  path='/' element={<Statspage/>}/>
                   <Route  path='/preselect' element={<PreselectedDesigns/>}/>
                   <Route  path='/select' element={<SelectedDesigns/>}/>
                   <Route  path='/winners' element={<WinnersDesigns/>}/>
                   <Route  path='/user' element={<UserDetails />} />

                </Routes>

            </div>

        </div>
    )
}