import React, { useEffect, useState } from "react";
import { getData } from "../../../services/NodeServices";
import './AllUsers.css'
import { useNavigate } from "react-router-dom";

const AllUsers = () => {
  const [users, setUsers] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [usersPerPage] = useState(10);
  const navigate = useNavigate()

  useEffect(() => {
    getuserData();
  }, []);

  async function getuserData() {
    try {
      const result = await getData('api/users/all');
      if (result.status) {
        setUsers(result.data);
      }
    } catch (error) {
      console.error("Failed to fetch user data:", error);
    }
  }

  const handleProfileClick = (userid) => {
    navigate(`/admin/user?userid=${userid}`);
  }

  const handleSearchChange = (event) => {
    setSearchTerm(event.target.value);
    setCurrentPage(1); // Reset to first page on search change
  }

  const filteredUsers = users.filter(user => 
    user.firstname.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.email.toLowerCase().includes(searchTerm.toLowerCase()) ||
    (user.from && user.from.toLowerCase().includes(searchTerm.toLowerCase()))
  );

  // Pagination Logic
  const indexOfLastUser = currentPage * usersPerPage;
  const indexOfFirstUser = indexOfLastUser - usersPerPage;
  const currentUsers = filteredUsers.slice(indexOfFirstUser, indexOfLastUser);

  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  return (
    <div className="custom-table-wrapper">
      <h1 className="all-users-heading">All Users</h1>

      <input 
        type="text" 
        className="search-input"
        placeholder="Search by name or email"
        value={searchTerm}
        onChange={handleSearchChange} 
      />

      <div className="custom-table">
        <div className="custom-table-header">
          <div className="custom-table-cell">#</div>
          <div className="custom-table-cell">First Name</div>
          <div className="custom-table-cell">Email</div>
          <div className="custom-table-cell">Came From</div>
          <div className="custom-table-cell">Actions</div>
        </div>

        {currentUsers.map((user, index) => (
          <div key={user.email} className="custom-table-row">
            <div className="custom-table-cell">{index + 1}</div>
            <div className="custom-table-cell">{user.firstname}</div>
            <div className="custom-table-cell">{user.email}</div>
            <div className="custom-table-cell">{user.from}</div>
            <div className="custom-table-cell">
              <button className="edit-button" onClick={() => handleProfileClick(user.userid)} >View</button>
            </div>
          </div>
        ))}
      </div>

      {/* Pagination */}
      <div className="pagination">
        {Array.from({ length: Math.ceil(filteredUsers.length / usersPerPage) }, (_, index) => (
          <button
            key={index + 1}
            className={`page-button ${currentPage === index + 1 ? "active" : ""}`}
            onClick={() => paginate(index + 1)}
          >
            {index + 1}
          </button>
        ))}
      </div>
    </div>
  );
};

export default AllUsers;
