import React, { useEffect } from 'react';


const ShippingPolicy = () => {
    useEffect(() => {
        window.scrollTo({
            top: 0,
            behavior: 'smooth',
        });
    }, []);

    return (
        <div className='maincont'>
            <div className="privacy-policy-container">
                <h1 className="privacy-policy-title">Shipping Policy</h1>
                <p>
                    Clanfest Art Competition is a digital-first event, and this Shipping Policy outlines the terms related to the distribution of materials, prizes, and certificates. By participating in the competition, you agree to the following:
                </p>

                <h2 className="privacy-policy-subtitle">1. No Physical Shipping</h2>
                <ul className="privacy-policy-list">
                    <li>This competition does not involve the physical shipment of any materials, documents, or resources to participants during or after registration.</li>
                    <li>All relevant competition details and updates will be provided digitally through the official Blaze website or via email.</li>
                </ul>

                <h2 className="privacy-policy-subtitle">2. Distribution of Winner Rewards</h2>
                <ul className="privacy-policy-list">
                    <li>
                        Prizes for winners, including medals, hampers, and other rewards, will be distributed through one of the following methods:
                        <ul className="privacy-policy-sublist">
                            <li>At an official event organized by Blaze (details to be communicated to winners).</li>
                            <li>Direct delivery facilitated via Blaze’s official channels.</li>
                        </ul>
                    </li>
                    <li>Specific instructions and timelines for prize distribution will be shared directly with the winners.</li>
                </ul>

                <h2 className="privacy-policy-subtitle">3. Certificates</h2>
                <ul className="privacy-policy-list">
                    <li>All participants, finalists, and winners will receive digital certificates.</li>
                    <li>These certificates will be accessible through the participant’s dashboard on the Blaze website and can be downloaded at their convenience.</li>
                    <li>Blaze is not responsible for any delays caused by technical issues, though reasonable support will be provided to resolve such matters promptly.</li>
                </ul>

                <p>
                    For further assistance or inquiries related to this policy, participants may contact Blaze at support@blazestore.in.
                </p>
                <p>
                    This Shipping Policy is binding upon all participants, and Blaze reserves the right to update or amend these terms as needed. Updated policies, if any, will be communicated via the official Blaze website.
                </p>
            </div>
        </div>
    );
};

export default ShippingPolicy;
